.App {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

.image-container {
  margin:3px 0;
  cursor: pointer;
  position: relative;
}

.text-overlay {
  background-color: #6c757d; /* 회색 배경 */
  color: white; /* 흰색 글자 */
  font-size: 20px;
  padding: 20px 0; /* 텍스트가 중앙에 오도록 패딩 추가 */
  text-align: center;
  width: 500px; /* 이미지와 동일한 너비 */
  padding: 10px 0;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  transition: background-color 0.3s ease;
}

.text-overlay.active {
  background-color: #3f4245; /* 클릭 시 약간 어두운 색으로 변경 */
}

.image-style {
  width: 500px;
  height: auto;
  margin-top: 2px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ddd; /* 이미지 테두리 추가 */
  z-index: 0;
}
